import { DisplayObject } from "pixi.js";
import { v4 } from "uuid";
import { Component } from "../Component";

export class EventHandler {
  readonly id = v4();
  readonly stops: EventType[] = [];

  constructor(readonly type: EventType, readonly sender: Component) {}

  protected hitTest(
    pos: Coordinate,
    obj: Component | DisplayObject
  ): Coordinate | null {
    if (obj instanceof Component) {
      obj = obj.ui;
    }
    const v: Float32Array = (<any>obj).vertexData;
    let { x, y } = pos;
    [x, y] = [x - v[0], y - v[1]];
    const [x1, y1] = [v[2] - v[0], v[3] - v[1]];
    const [x2, y2] = [v[6] - v[0], v[7] - v[1]];
    const x2_y1_x1_y2 = x2 * y1 - x1 * y2;
    let a = (x2 * y - x * y2) / x2_y1_x1_y2;
    let b = (x * y1 - x1 * y) / x2_y1_x1_y2;
    if (0 <= a && a <= 1 && 0 <= b && b <= 1) {
      return { x: a, y: b };
    }
    return null;
  }

  start() {}

  finish() {}

  _bootstrap(): EventHandler {
    return Object.setPrototypeOf({}, this);
  }

  _pointermove(pos: Coordinate): EventHandler {
    return null;
  }

  _pointerdown(pos: Coordinate): EventHandler {
    return null;
  }

  _pointerup(pos: Coordinate): EventHandler {
    return null;
  }

  _pointerout(pos: Coordinate): EventHandler {
    return null;
  }

  _wheel(pos: Coordinate, evt: WheelEvent): EventHandler {
    return null;
  }
}
